  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        lists: [],
        lists1: [],
        current: 0,
        sliderAnim: {},
				isLogin: this.$store.state.isLogin
      }
    },
    onLoad() {
      // let id = uni.getStorageSync('user').id
			// let id = JSON.parse(uni.getStorageSync('userInfo')).id
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
      uni.request({
        url: urls.HTTP + '/user/mycomment',
        method: 'POST',
        data: {
          id: id
        },
        success: (res) => {
          // console.log(res)
          if (res.data.code === 0) {
            this.lists = res.data.commentlist
          } else {
            uni.showToast({
              title: res.data.error,
              icon: 'none'
            })
          }
        }
      })
    },
    onShow (){
      // 创建一个动画实例
    	var animation = uni.createAnimation({
    		duration: 500,
        timingFunction: 'ease'
    	})
      
      this.animation = animation
    },
    methods: {
      changeClass: function (n) {
        this.current = n
        // let id = uni.getStorageSync('user').id			
				// let id = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        if (n === 0) {
          this.animation.left(0).step()
          this.sliderAnim = this.animation.export()
          uni.request({
            url: urls.HTTP + '/user/mycomment',
            method: 'POST',
            data: {
              id: id
            },
            success: (res) => {
              // console.log(res)
              if (res.data.code === 0) {
                this.lists = res.data.commentlist
              } else {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
                this.lists = []
              }
            }
          })
        } else {
          this.animation.left(100 + '%').step()
          this.sliderAnim = this.animation.export()
          uni.request({
            url: urls.HTTP + '/user/myreplycomment',
            method: 'POST',
            data: {
              id: id
            },
            success: (res) => {
              console.log(res)
              if (res.data.code === 0) {
                this.lists1 = res.data.commentlist
              } else {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
                this.lists1 = []
              }
            }
          })
        }
      },
      goDetail: function (val) {
        this.$store.dispatch('comment', val)
        uni.navigateTo({
          url: 'page/myReply'
        })
      },
      delThis: function (item) {
        uni.showModal({
          title: '提示',
          content: '是否删除此书评？',
          success: (res) => {
            if (res.confirm) {
              uni.showToast({
                title: '删除成功!'
              })
            }
          }
        })
      },
      imgUrl: function (url) {
        return urls.UrlHttp + url
      }
    },
    computed: {
      isOne () {
        if (this.current === 0) {
          return true
        } else if (this.current === 1) {
          return false
        }
      }
    }
  }
